<template>
	<div class="home_view">
		<div class="projectTitle">欢迎使用 {{projectName}}</div>
		<div class="count_list">
			<el-collapse-transition v-if="btnAuth('chengjixinxi','首页总数')">
				<el-card v-show="countTypeList.closechengjixinxiCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								成绩信息
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenchengjixinxiCountType')" class="showIcons"
									:class="countTypeList.hiddenchengjixinxiCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closechengjixinxiCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenchengjixinxiCountType">
							<div class="count_title">成绩信息总数</div>
							<div class="count_num">{{chengjixinxiCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('xuexiqushi','首页总数')">
				<el-card v-show="countTypeList.closexuexiqushiCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								学习趋势
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenxuexiqushiCountType')" class="showIcons"
									:class="countTypeList.hiddenxuexiqushiCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closexuexiqushiCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenxuexiqushiCountType">
							<div class="count_title">学习趋势总数</div>
							<div class="count_num">{{xuexiqushiCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
		<div class="card_list">
			<el-collapse-transition v-if="btnAuth('chengjixinxi','首页统计')">
				<el-card v-show="cardTypeList.closechengjixinxiChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								成绩信息
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenchengjixinxiChartType1')" class="showIcons"
									:class="cardTypeList.hiddenchengjixinxiChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closechengjixinxiChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenchengjixinxiChartType1">
							<div id="chengjixinxifenshuEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('xuexiqushi','首页统计')">
				<el-card v-show="cardTypeList.closexuexiqushiChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								学习趋势
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenxuexiqushiChartType1')" class="showIcons"
									:class="cardTypeList.hiddenxuexiqushiChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closexuexiqushiChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenxuexiqushiChartType1">
							<div id="xuexiqushipingjunfenEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
	</div>
</template>

<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const countTypeList = ref({
	})
	const getCountList=()=>{
		countTypeList.value.closechengjixinxiCountType = true
		countTypeList.value.hiddenchengjixinxiCountType = true
		if(btnAuth('chengjixinxi','首页总数')){
			getchengjixinxiCount()
		}
		countTypeList.value.closexuexiqushiCountType = true
		countTypeList.value.hiddenxuexiqushiCountType = true
		if(btnAuth('xuexiqushi','首页总数')){
			getxuexiqushiCount()
		}
	}
	const chengjixinxiCount = ref(0)
	const getchengjixinxiCount = () => {
		context?.$http({
			url:'chengjixinxi/count',
			method: 'get'
		}).then(res=>{
			chengjixinxiCount.value = res.data.data
		})
	}
	const xuexiqushiCount = ref(0)
	const getxuexiqushiCount = () => {
		context?.$http({
			url:'xuexiqushi/count',
			method: 'get'
		}).then(res=>{
			xuexiqushiCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	const init=()=>{
		getCountList()
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closechengjixinxiChartType1: true,
		hiddenchengjixinxiChartType1: true,
		closexuexiqushiChartType1: true,
		hiddenxuexiqushiChartType1: true,
	})
	const getCardList = () => {
		if(btnAuth('chengjixinxi','首页统计')){
			getchengjixinxiChart1()
		}
		if(btnAuth('xuexiqushi','首页统计')){
			getxuexiqushiChart1()
		}
	}
	const getchengjixinxiChart1 = () => {
		nextTick(()=>{
			var fenshuEchart1 = echarts.init(document.getElementById("chengjixinxifenshuEchart1"),'macarons');
			context?.$http({
				url: "chengjixinxi/sectionStat/fenshu/kaoshimingcheng",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis1 = [];
				let yAxis2 = [];
				let yAxis3 = [];
				let yAxis4 = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].kaoshimingcheng);
					yAxis1.push(parseFloat((res[i].不及格)));
					yAxis2.push(parseFloat((res[i].及格)));
					yAxis3.push(parseFloat((res[i].良)));
					yAxis4.push(parseFloat((res[i].优秀)));
					pArray.push({
					    name: res[i].fenshu
					})
				}
				var option = {};
                option = {
                    title: {
                        text: '成绩统计',
                        left: 'center'
                    },
                    legend: {
						data: [
							'不及格',
							'及格',
							'良',
							'优秀',
                        ],
						left: 'center',
						bottom: 10,     
                    },
                    tooltip: {
                      trigger: 'axis'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
						{
							name: '不及格',
							data: yAxis1,
							type: 'bar'
						},
						{
							name: '及格',
							data: yAxis2,
							type: 'bar'
						},
						{
							name: '良',
							data: yAxis3,
							type: 'bar'
						},
						{
							name: '优秀',
							data: yAxis4,
							type: 'bar'
						},
                    ],
                };
				fenshuEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				fenshuEchart1.setOption(option);
				//根据窗口的大小变动图表
				fenshuEchart1.resize();
			})
		})
	}
	const getxuexiqushiChart1 = () => {
		nextTick(()=>{
			var pingjunfenEchart1 = echarts.init(document.getElementById("xuexiqushipingjunfenEchart1"),'macarons');
			context?.$http({
				url: `xuexiqushi/value/xuenianxueqi/pingjunfen`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].xuenianxueqi);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].xuenianxueqi
				    })
				}
				var option = {};
				option = {
				    title: {
				        text: '平均分趋势',
				        left: 'center'
				    },
				    tooltip: {
				      trigger: 'item',
				      formatter: '{b} : {c}'
				    },
				    xAxis: {
				        type: 'category',
				        boundaryGap: false,
				        data: xAxis
				    },
				    yAxis: {
				        type: 'value'
				    },
				    series: [{
				        data: yAxis,
				        type: 'line',
                    }]
                };
				pingjunfenEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				pingjunfenEchart1.setOption(option);
				//根据窗口的大小变动图表
				pingjunfenEchart1.resize();
			})
		})
	}
	init()
</script>
<style lang="scss">
	.projectTitle{
		padding: 20px 0;
		margin: 20px 0 20px;
		color: #333;
		font-weight: bold;
		display: flex;
		width: 100%;
		font-size: 30px;
		justify-content: center;
		align-items: center;
		height: auto;
	}

	.showIcons {
		transition: transform 0.3s;
		margin-right: 10px;
	}

	.showIcons1 {
		transform: rotate(-180deg);
	}
	
	// 总数盒子
	.count_list{
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 总数card
		.card_view {
			border: 0px solid #fccb8250;
			border-radius: 0;
			box-shadow: 0px 0px 0px rgba(0,0,0,.12);
			margin: 0 1% 20px;
			width: 23%;
			box-sizing: border-box;
			height: auto;
			// card头部
			.el-card__header {
				border: 0px solid #eee;
				background: linear-gradient(180deg, rgba(193,239,255,1) 0%, rgba(41,139,161,1) 100%);
				width: 100%;
				border-width: 0 0 0px;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					height: 10px;
					// 标题
					.card_head_title {
						color: #333;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon {
							cursor: pointer;
							color: #333;
							font-size: 20px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				background: #fff;
				border-color: #eee;
				border-width: 0 1px 1px;
				border-style: solid;
				// body盒子
				.count_item{
					padding: 30px;
					display: flex;
					justify-content: center;
					align-items: center;
					text-align: center;
					// 总数标题
					.count_title{
						margin: 0 10px 0 0;
						color: #666;
						font-weight: 500;
						display: inline-block;
						font-size: 16px;
						line-height: 1.5;
					}
					// 总数数字
					.count_num{
						color: #c00;
						font-weight: 600;
						display: inline-block;
						font-size: 26px;
						line-height: 1.5;
					}
				}
			}
		}
	}
	// 首页盒子
	.home_view {
		padding: 0px 20px;
		margin: 0;
		background: url(http://clfile.zggen.cn/20240418/c852d42abf3c4bbc873ed4c1350dccce.png) no-repeat center top / 100% 100% !important;
		width: 100%;
		min-height: 100vh;
		height: auto;
	}
	// 统计图盒子
	.card_list {
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: space-between;
		align-items: flex-start;
		flex-wrap: wrap;
		// 统计图card
		.card_view {
			border: 0px solid #fccb8250;
			border-radius: 0;
			box-shadow: 0px 0px 0px rgba(0,0,0,.12);
			margin: 10px;
			width: calc(33.33% - 20px);
			box-sizing: border-box;
			height: auto;
			// 头部
			.el-card__header {
				border: 0px solid #eee;
				background: linear-gradient(180deg, rgba(193,239,255,1) 0%, rgba(41,139,161,1) 100%);
				width: 100%;
				border-width: 0 0 0px;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					height: 10px;
					// 标题
					.card_head_title {
						color: #333;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon{
							cursor: pointer;
							color: #333;
							font-size: 20px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				background: #fff;
				border-color: #eee;
				border-width: 0 1px 1px;
				border-style: solid;
				// body盒子
				.card_item{
					padding: 10px;
					text-align: center;
				}
			}
		}
	}
</style>
